'use client';

import { Token as TokenType, PlayerColor } from '@/lib/game-state';
import { cn } from '@/lib/utils';

interface TokenProps {
  token: TokenType;
  onClick?: () => void;
  isSelected?: boolean;
  isMovable?: boolean;
}

const colorClasses: Record<PlayerColor, string> = {
  red: 'bg-red-500 border-red-700',
  blue: 'bg-blue-500 border-blue-700',
  green: 'bg-green-500 border-green-700',
  yellow: 'bg-yellow-500 border-yellow-700',
};

export function Token({ token, onClick, isSelected, isMovable }: TokenProps) {
  return (
    <button
      onClick={onClick}
      disabled={!isMovable}
      className={cn(
        'w-8 h-8 rounded-full border-2 transition-all duration-200',
        'hover:scale-110 active:scale-95',
        'disabled:opacity-50 disabled:cursor-not-allowed',
        colorClasses[token.color],
        isSelected && 'ring-2 ring-white ring-offset-2 ring-offset-gray-800',
        isMovable && 'cursor-pointer hover:shadow-lg'
      )}
      aria-label={`${token.color} token ${token.id + 1}`}
    >
      <span className="text-white text-xs font-bold">{token.id + 1}</span>
    </button>
  );
}
