export interface Token {
  id: number;
  color: PlayerColor;
  position: number;
  isHome: boolean;
  isSafe: boolean;
}

export interface Player {
  id: number;
  color: PlayerColor;
  name: string;
  tokens: Token[];
  isCurrent: boolean;
}

export type PlayerColor = 'red' | 'blue' | 'green' | 'yellow';

export interface GameState {
  players: Player[];
  currentPlayerIndex: number;
  diceValue: number;
  isRolling: boolean;
  gameStarted: boolean;
  winner: PlayerColor | null;
}

export const PLAYER_COLORS: PlayerColor[] = ['red', 'blue', 'green', 'yellow'];
export const TOKEN_COUNT = 4;
export const BOARD_SIZE = 52;
export const HOME_STRETCH_SIZE = 6;

export const createInitialGameState = (): GameState => ({
  players: PLAYER_COLORS.map((color, index) => ({
    id: index,
    color,
    name: color.charAt(0).toUpperCase() + color.slice(1),
    tokens: Array.from({ length: TOKEN_COUNT }, (_, i) => ({
      id: i,
      color,
      position: -1,
      isHome: false,
      isSafe: false,
    })),
    isCurrent: index === 0,
  })),
  currentPlayerIndex: 0,
  diceValue: 0,
  isRolling: false,
  gameStarted: false,
  winner: null,
});

export const isSafePosition = (position: number): boolean => {
  const safePositions = [0, 8, 13, 21, 26, 34, 39, 47];
  return safePositions.includes(position);
};

export const canMoveToken = (token: Token, diceValue: number): boolean => {
  if (token.isHome) return false;
  if (token.position === -1 && diceValue !== 6) return false;
  return true;
};

export const getNextPosition = (
  currentPosition: number,
  diceValue: number,
  playerColor: PlayerColor
): number => {
  if (currentPosition === -1 && diceValue === 6) return 0;
  if (currentPosition === -1) return -1;
  
  const nextPosition = (currentPosition + diceValue) % BOARD_SIZE;
  return nextPosition;
};
