'use client';

import { PlayerColor } from '@/lib/game-state';
import { Dice } from './dice';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';

interface GameControlsProps {
  diceValue: number;
  isRolling: boolean;
  onRollDice: () => void;
  onResetGame: () => void;
  gameStarted: boolean;
  winner: PlayerColor | null;
  currentPlayerName: string;
  canRoll: boolean;
}

const winnerColors: Record<PlayerColor, string> = {
  red: 'text-red-600',
  blue: 'text-blue-600',
  green: 'text-green-600',
  yellow: 'text-yellow-600',
};

export function GameControls({
  diceValue,
  isRolling,
  onRollDice,
  onResetGame,
  gameStarted,
  winner,
  currentPlayerName,
  canRoll,
}: GameControlsProps) {
  return (
    <div className="bg-white rounded-lg shadow-lg p-6">
      <div className="text-center space-y-4">
        {winner ? (
          <div className="space-y-4">
            <div className="text-2xl font-bold">üéâ Game Over! üéâ</div>
            <div className={cn('text-xl font-semibold', winnerColors[winner])}>
              {winner.charAt(0).toUpperCase() + winner.slice(1)} Wins!
            </div>
            <Button 
              onClick={onResetGame}
              className="w-full bg-green-600 hover:bg-green-700"
            >
              Play Again
            </Button>
          </div>
        ) : (
          <div className="space-y-4">
            {!gameStarted ? (
              <div className="space-y-4">
                <h2 className="text-2xl font-bold text-gray-800">Ludo Game</h2>
                <p className="text-gray-600">Roll the dice to start playing!</p>
                <Dice
                  value={diceValue || 1}
                  isRolling={isRolling}
                  onRoll={onRollDice}
                  disabled={false}
                />
              </div>
            ) : (
              <div className="space-y-4">
                <div className="text-lg font-semibold text-gray-800">
                  {currentPlayerName}'s Turn
                </div>
                
                <Dice
                  value={diceValue || 1}
                  isRolling={isRolling}
                  onRoll={onRollDice}
                  disabled={!canRoll || isRolling}
                />
                
                {diceValue > 0 && !isRolling && (
                  <div className="text-sm text-gray-600">
                    {canRoll ? 'Click a token to move' : 'No valid moves available'}
                  </div>
                )}
                
                <div className="space-y-2">
                  <Button 
                    onClick={onResetGame}
                    variant="outline"
                    className="w-full"
                  >
                    Reset Game
                  </Button>
                </div>
              </div>
            )}
          </div>
        )}
        
        {gameStarted && !winner && (
          <div className="text-xs text-gray-500 space-y-1">
            <div>‚Ä¢ Roll 6 to move tokens out of home</div>
            <div>‚Ä¢ Land on opponents to send them home</div>
            <div>‚Ä¢ Get all tokens to the center to win</div>
          </div>
        )}
      </div>
    </div>
  );
}
