'use client';

import { useState, useEffect } from 'react';
import { cn } from '@/lib/utils';

interface DiceProps {
  value: number;
  isRolling: boolean;
  onRoll: () => void;
  disabled?: boolean;
}

const DiceFace = ({ value }: { value: number }) => {
  const getDotPositions = (num: number) => {
    const positions = {
      1: ['center'],
      2: ['top-left', 'bottom-right'],
      3: ['top-left', 'center', 'bottom-right'],
      4: ['top-left', 'top-right', 'bottom-left', 'bottom-right'],
      5: ['top-left', 'top-right', 'center', 'bottom-left', 'bottom-right'],
      6: ['top-left', 'top-right', 'middle-left', 'middle-right', 'bottom-left', 'bottom-right'],
    };
    return positions[num as keyof typeof positions] || [];
  };

  const dotPositions = getDotPositions(value);

  return (
    <div className="relative w-12 h-12 grid grid-cols-3 grid-rows-3 gap-1 p-1">
      {Array.from({ length: 9 }).map((_, index) => {
        const positions = [
          'top-left', 'top-center', 'top-right',
          'middle-left', 'center', 'middle-right',
          'bottom-left', 'bottom-center', 'bottom-right'
        ];
        const position = positions[index];
        const shouldShow = dotPositions.includes(position);
        
        return (
          <div
            key={index}
            className={cn(
              'w-2 h-2 rounded-full transition-all duration-200',
              shouldShow ? 'bg-gray-800' : 'bg-transparent'
            )}
          />
        );
      })}
    </div>
  );
};

export function Dice({ value, isRolling, onRoll, disabled }: DiceProps) {
  const [displayValue, setDisplayValue] = useState(value);

  useEffect(() => {
    if (isRolling) {
      const interval = setInterval(() => {
        setDisplayValue(Math.floor(Math.random() * 6) + 1);
      }, 100);
      
      setTimeout(() => {
        clearInterval(interval);
        setDisplayValue(value);
      }, 1000);

      return () => clearInterval(interval);
    } else {
      setDisplayValue(value);
    }
  }, [isRolling, value]);

  return (
    <button
      onClick={onRoll}
      disabled={disabled || isRolling}
      className={cn(
        'w-16 h-16 bg-white rounded-lg shadow-lg border-2 border-gray-300',
        'flex items-center justify-center transition-all duration-200',
        'hover:shadow-xl active:scale-95',
        'disabled:opacity-50 disabled:cursor-not-allowed',
        isRolling && 'animate-bounce'
      )}
      aria-label="Roll dice"
    >
      <DiceFace value={displayValue} />
    </button>
  );
}
