'use client';

import { useState, useCallback } from 'react';
import {
  GameState,
  createInitialGameState,
  canMoveToken,
  getNextPosition,
  isSafePosition,
  PlayerColor,
  BOARD_SIZE,
} from '@/lib/game-state';

export function useLudoGame() {
  const [gameState, setGameState] = useState<GameState>(createInitialGameState());
  const [selectedToken, setSelectedToken] = useState<{ playerId: number; tokenId: number } | null>(null);

  const rollDice = useCallback(() => {
    if (gameState.isRolling || gameState.winner) return;

    setGameState(prev => ({ ...prev, isRolling: true }));
    
    setTimeout(() => {
      const diceValue = Math.floor(Math.random() * 6) + 1;
      setGameState(prev => ({
        ...prev,
        diceValue,
        isRolling: false,
        gameStarted: true,
      }));
      
      // Check for valid moves after a short delay
      setTimeout(() => {
        setGameState(current => {
          const currentPlayer = current.players[current.currentPlayerIndex];
          const hasValidMoves = currentPlayer.tokens.some(token => canMoveToken(token, diceValue));
          
          // If no valid moves, automatically move to next player after showing the result
          if (!hasValidMoves) {
            return {
              ...current,
              currentPlayerIndex: (current.currentPlayerIndex + 1) % current.players.length,
              diceValue: 0,
            };
          }
          
          return current;
        });
      }, 2000); // Show "No valid moves" for 2 seconds before moving to next player
    }, 1000);
  }, [gameState.isRolling, gameState.winner]);

  const getMovableTokens = useCallback(() => {
    const currentPlayer = gameState.players[gameState.currentPlayerIndex];
    if (!currentPlayer || gameState.diceValue === 0) return [];

    return currentPlayer.tokens
      .filter(token => canMoveToken(token, gameState.diceValue))
      .map(token => ({ playerId: currentPlayer.id, tokenId: token.id }));
  }, [gameState.players, gameState.currentPlayerIndex, gameState.diceValue]);

  const moveToken = useCallback((playerId: number, tokenId: number) => {
    const player = gameState.players.find(p => p.id === playerId);
    const token = player?.tokens.find(t => t.id === tokenId);
    
    if (!player || !token || !canMoveToken(token, gameState.diceValue)) return;

    setGameState(prev => {
      const newState = { ...prev };
      const newPlayers = [...newState.players];
      const playerIndex = newPlayers.findIndex(p => p.id === playerId);
      const newPlayer = { ...newPlayers[playerIndex] };
      const newTokens = [...newPlayer.tokens];
      const tokenIndex = newTokens.findIndex(t => t.id === tokenId);
      const newToken = { ...newTokens[tokenIndex] };

      // Calculate new position
      const newPosition = getNextPosition(newToken.position, newState.diceValue, newToken.color);
      
      // Handle token entering the board
      if (newToken.position === -1 && newState.diceValue === 6) {
        newToken.position = getStartPosition(newToken.color);
      } else if (newToken.position >= 0) {
        // Handle normal movement
        const targetPosition = (newToken.position + newState.diceValue) % BOARD_SIZE;
        
        // Check if token reaches home stretch
        if (shouldEnterHomeStretch(newToken.position, newState.diceValue, newToken.color)) {
          const homeStretchStart = 52 + (playerId * 6);
          const stepsIntoHomeStretch = (newToken.position + newState.diceValue) - getHomeStretchEntryPosition(newToken.color);
          newToken.position = homeStretchStart + stepsIntoHomeStretch;
          
          // Check if token reaches home
          if (newToken.position >= homeStretchStart + 6) {
            newToken.isHome = true;
            newToken.position = homeStretchStart + 5; // Final position
          }
        } else {
          newToken.position = targetPosition;
        }
      }

      // Update token safety status
      newToken.isSafe = isSafePosition(newToken.position);

      // Handle capturing opponent tokens
      if (!newToken.isSafe && newToken.position >= 0 && newToken.position < BOARD_SIZE) {
        newPlayers.forEach((otherPlayer, otherPlayerIndex) => {
          if (otherPlayer.id !== playerId) {
            otherPlayer.tokens.forEach((otherToken, otherTokenIndex) => {
              if (otherToken.position === newToken.position && !otherToken.isSafe) {
                newPlayers[otherPlayerIndex] = {
                  ...otherPlayer,
                  tokens: otherPlayer.tokens.map((t, i) =>
                    i === otherTokenIndex ? { ...t, position: -1, isSafe: false } : t
                  ),
                };
              }
            });
          }
        });
      }

      // Update the moved token
      newTokens[tokenIndex] = newToken;
      newPlayer.tokens = newTokens;
      newPlayers[playerIndex] = newPlayer;

      // Check for winner
      const winner = checkWinner(newPlayers);
      
      // Determine next player (stay if rolled 6 or captured, otherwise next player)
      let nextPlayerIndex = newState.currentPlayerIndex;
      if (newState.diceValue !== 6) {
        nextPlayerIndex = (newState.currentPlayerIndex + 1) % newPlayers.length;
      }

      return {
        ...newState,
        players: newPlayers,
        currentPlayerIndex: nextPlayerIndex,
        diceValue: 0,
        winner,
      };
    });

    setSelectedToken(null);
  }, [gameState]);

  const getStartPosition = (color: PlayerColor): number => {
    const startPositions = { red: 1, blue: 14, green: 27, yellow: 40 };
    return startPositions[color];
  };

  const getHomeStretchEntryPosition = (color: PlayerColor): number => {
    const entryPositions = { red: 51, blue: 12, green: 25, yellow: 38 };
    return entryPositions[color];
  };

  const shouldEnterHomeStretch = (currentPosition: number, diceValue: number, color: PlayerColor): boolean => {
    const entryPosition = getHomeStretchEntryPosition(color);
    const newPosition = currentPosition + diceValue;
    return currentPosition < entryPosition && newPosition >= entryPosition;
  };

  const checkWinner = (players: typeof gameState.players): PlayerColor | null => {
    for (const player of players) {
      if (player.tokens.every(token => token.isHome)) {
        return player.color;
      }
    }
    return null;
  };

  const resetGame = useCallback(() => {
    setGameState(createInitialGameState());
    setSelectedToken(null);
  }, []);

  const handleTokenClick = useCallback((playerId: number, tokenId: number) => {
    if (gameState.currentPlayerIndex !== playerId || gameState.diceValue === 0) return;
    
    const movableTokens = getMovableTokens();
    const isMovable = movableTokens.some(mt => mt.playerId === playerId && mt.tokenId === tokenId);
    
    if (isMovable) {
      if (selectedToken?.playerId === playerId && selectedToken?.tokenId === tokenId) {
        // Move the selected token
        moveToken(playerId, tokenId);
      } else {
        // Select the token
        setSelectedToken({ playerId, tokenId });
      }
    }
  }, [gameState.currentPlayerIndex, gameState.diceValue, getMovableTokens, moveToken, selectedToken]);

  return {
    gameState,
    selectedToken,
    movableTokens: getMovableTokens(),
    rollDice,
    handleTokenClick,
    resetGame,
  };
}
