'use client';

import { useLudoGame } from '@/hooks/use-ludo-game';
import { LudoBoard } from '@/components/ludo-board';
import { PlayerPanel } from '@/components/player-panel';
import { GameControls } from '@/components/game-controls';

export default function LudoGamePage() {
  const {
    gameState,
    selectedToken,
    movableTokens,
    rollDice,
    handleTokenClick,
    resetGame,
  } = useLudoGame();

  const currentPlayer = gameState.players[gameState.currentPlayerIndex];
  const canRoll = gameState.diceValue === 0 && !gameState.isRolling && !gameState.winner;

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 p-4">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-6">
          <h1 className="text-4xl font-bold text-gray-800 mb-2">Ludo Game</h1>
          <p className="text-gray-600">Classic board game for 2-4 players</p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Game Board - Takes up most space */}
          <div className="lg:col-span-3 flex justify-center">
            <LudoBoard
              players={gameState.players}
              onTokenClick={handleTokenClick}
              selectedToken={selectedToken}
              movableTokens={movableTokens}
            />
          </div>

          {/* Side Panel */}
          <div className="lg:col-span-1 space-y-4">
            {/* Game Controls */}
            <GameControls
              diceValue={gameState.diceValue}
              isRolling={gameState.isRolling}
              onRollDice={rollDice}
              onResetGame={resetGame}
              gameStarted={gameState.gameStarted}
              winner={gameState.winner}
              currentPlayerName={currentPlayer?.name || ''}
              canRoll={canRoll}
            />

            {/* Player Panel */}
            <PlayerPanel
              players={gameState.players}
              currentPlayerIndex={gameState.currentPlayerIndex}
              diceValue={gameState.diceValue}
            />
          </div>
        </div>

        {/* Game Instructions */}
        <div className="mt-8 bg-white rounded-lg shadow-lg p-6">
          <h3 className="text-lg font-semibold mb-4">How to Play</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm text-gray-600">
            <div>
              <h4 className="font-semibold text-gray-800 mb-2">Basic Rules:</h4>
              <ul className="space-y-1">
                <li>‚Ä¢ Roll a 6 to move tokens out of home</li>
                <li>‚Ä¢ Move tokens clockwise around the board</li>
                <li>‚Ä¢ Land on opponents to send them home</li>
                <li>‚Ä¢ Safe squares protect your tokens</li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold text-gray-800 mb-2">Winning:</h4>
              <ul className="space-y-1">
                <li>‚Ä¢ Get all 4 tokens to the center</li>
                <li>‚Ä¢ Tokens must enter the home stretch</li>
                <li>‚Ä¢ Exact count needed to reach home</li>
                <li>‚Ä¢ First player to finish wins!</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
