'use client';

import { Player, Token as TokenType, PlayerColor } from '@/lib/game-state';
import { Token } from './token';
import { cn } from '@/lib/utils';

interface LudoBoardProps {
  players: Player[];
  onTokenClick: (playerId: number, tokenId: number) => void;
  selectedToken?: { playerId: number; tokenId: number } | null;
  movableTokens: Array<{ playerId: number; tokenId: number }>;
}

const playerColors: Record<PlayerColor, string> = {
  red: 'bg-red-100 border-red-300',
  blue: 'bg-blue-100 border-blue-300',
  green: 'bg-green-100 border-green-300',
  yellow: 'bg-yellow-100 border-yellow-300',
};

const homeColors: Record<PlayerColor, string> = {
  red: 'bg-red-200',
  blue: 'bg-blue-200',
  green: 'bg-green-200',
  yellow: 'bg-yellow-200',
};

export function LudoBoard({ players, onTokenClick, selectedToken, movableTokens }: LudoBoardProps) {
  const renderHomeArea = (color: PlayerColor, position: string) => {
    const player = players.find(p => p.color === color);
    if (!player) return null;

    const homeTokens = player.tokens.filter(token => token.position === -1);

    return (
      <div className={cn(
        'w-24 h-24 border-2 rounded-lg p-2',
        homeColors[color],
        playerColors[color]
      )}>
        <div className="grid grid-cols-2 gap-1 h-full">
          {Array.from({ length: 4 }).map((_, index) => (
            <div key={index} className="flex items-center justify-center">
              {homeTokens[index] && (
                <Token
                  token={homeTokens[index]}
                  onClick={() => onTokenClick(player.id, homeTokens[index].id)}
                  isSelected={
                    selectedToken?.playerId === player.id &&
                    selectedToken?.tokenId === homeTokens[index].id
                  }
                  isMovable={movableTokens.some(
                    mt => mt.playerId === player.id && mt.tokenId === homeTokens[index].id
                  )}
                />
              )}
            </div>
          ))}
        </div>
      </div>
    );
  };

  const renderPathCell = (index: number) => {
    const tokensOnCell = players.flatMap(player =>
      player.tokens
        .filter(token => token.position === index)
        .map(token => ({ ...token, playerId: player.id }))
    );

    const isSafeCell = [0, 8, 13, 21, 26, 34, 39, 47].includes(index);

    return (
      <div
        key={index}
        className={cn(
          'w-8 h-8 border border-gray-300 flex items-center justify-center relative',
          isSafeCell && 'bg-yellow-100 border-yellow-400'
        )}
      >
        {tokensOnCell.length > 0 && (
          <div className="absolute inset-0 flex items-center justify-center">
            {tokensOnCell.length === 1 ? (
              <Token
                token={tokensOnCell[0]}
                onClick={() => onTokenClick(tokensOnCell[0].playerId, tokensOnCell[0].id)}
                isSelected={
                  selectedToken?.playerId === tokensOnCell[0].playerId &&
                  selectedToken?.tokenId === tokensOnCell[0].id
                }
                isMovable={movableTokens.some(
                  mt => mt.playerId === tokensOnCell[0].playerId && mt.tokenId === tokensOnCell[0].id
                )}
              />
            ) : (
              <div className="text-xs font-bold">{tokensOnCell.length}</div>
            )}
          </div>
        )}
      </div>
    );
  };

  const renderHomeStretch = (color: PlayerColor) => {
    const player = players.find(p => p.color === color);
    if (!player) return null;

    return (
      <div className={cn('flex', color === 'red' || color === 'yellow' ? 'flex-col' : 'flex-row')}>
        {Array.from({ length: 6 }).map((_, index) => {
          const stretchPosition = 52 + (player.id * 6) + index;
          const tokensOnCell = player.tokens.filter(token => token.position === stretchPosition);

          return (
            <div
              key={index}
              className={cn(
                'w-8 h-8 border border-gray-300 flex items-center justify-center',
                homeColors[color]
              )}
            >
              {tokensOnCell.length > 0 && (
                <Token
                  token={tokensOnCell[0]}
                  onClick={() => onTokenClick(player.id, tokensOnCell[0].id)}
                  isSelected={
                    selectedToken?.playerId === player.id &&
                    selectedToken?.tokenId === tokensOnCell[0].id
                  }
                  isMovable={movableTokens.some(
                    mt => mt.playerId === player.id && mt.tokenId === tokensOnCell[0].id
                  )}
                />
              )}
            </div>
          );
        })}
      </div>
    );
  };

  return (
    <div className="bg-white p-4 rounded-lg shadow-lg">
      <div className="grid grid-cols-15 gap-0 w-fit mx-auto">
        {/* Top section */}
        <div className="col-span-6 grid grid-cols-6">
          {Array.from({ length: 6 }).map((_, i) => renderPathCell(i))}
        </div>
        <div className="col-span-3 flex items-center justify-center">
          {renderHomeArea('green', 'top-left')}
        </div>
        <div className="col-span-6 grid grid-cols-6">
          {Array.from({ length: 6 }).map((_, i) => renderPathCell(i + 6))}
        </div>

        {/* Middle section */}
        <div className="col-span-6 flex flex-col">
          {Array.from({ length: 3 }).map((_, i) => (
            <div key={i} className="flex">
              {Array.from({ length: 6 }).map((_, j) => renderPathCell(47 - i * 6 - j))}
            </div>
          ))}
        </div>
        
        <div className="col-span-3 grid grid-cols-3 gap-1">
          <div>{renderHomeArea('red', 'top-left')}</div>
          <div className="flex items-center justify-center">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center">
              <span className="text-2xl font-bold text-gray-600">üè†</span>
            </div>
          </div>
          <div>{renderHomeArea('blue', 'top-right')}</div>
          
          <div className="flex items-center justify-center">{renderHomeStretch('red')}</div>
          <div></div>
          <div className="flex items-center justify-center">{renderHomeStretch('blue')}</div>
          
          <div>{renderHomeArea('yellow', 'bottom-left')}</div>
          <div className="flex items-center justify-center">{renderHomeStretch('yellow')}</div>
          <div>{renderHomeArea('green', 'bottom-right')}</div>
        </div>

        <div className="col-span-6 flex flex-col">
          {Array.from({ length: 3 }).map((_, i) => (
            <div key={i} className="flex">
              {Array.from({ length: 6 }).map((_, j) => renderPathCell(13 + i * 6 + j))}
            </div>
          ))}
        </div>

        {/* Bottom section */}
        <div className="col-span-6 grid grid-cols-6">
          {Array.from({ length: 6 }).map((_, i) => renderPathCell(31 + i))}
        </div>
        <div className="col-span-3 flex items-center justify-center">
          {renderHomeStretch('green')}
        </div>
        <div className="col-span-6 grid grid-cols-6">
          {Array.from({ length: 6 }).map((_, i) => renderPathCell(37 + i))}
        </div>
      </div>
    </div>
  );
}
