'use client';

import { Player, PlayerColor } from '@/lib/game-state';
import { cn } from '@/lib/utils';

interface PlayerPanelProps {
  players: Player[];
  currentPlayerIndex: number;
  diceValue: number;
}

const playerColorClasses: Record<PlayerColor, string> = {
  red: 'bg-red-500 text-white',
  blue: 'bg-blue-500 text-white',
  green: 'bg-green-500 text-white',
  yellow: 'bg-yellow-500 text-white',
};

const playerBorderClasses: Record<PlayerColor, string> = {
  red: 'border-red-500',
  blue: 'border-blue-500',
  green: 'border-green-500',
  yellow: 'border-yellow-500',
};

export function PlayerPanel({ players, currentPlayerIndex, diceValue }: PlayerPanelProps) {
  return (
    <div className="bg-white rounded-lg shadow-lg p-4">
      <h2 className="text-xl font-bold text-center mb-4">Players</h2>
      
      <div className="space-y-3">
        {players.map((player, index) => {
          const isCurrentPlayer = index === currentPlayerIndex;
          const tokensAtHome = player.tokens.filter(token => token.position === -1).length;
          const tokensOnBoard = player.tokens.filter(token => token.position >= 0 && !token.isHome).length;
          const tokensFinished = player.tokens.filter(token => token.isHome).length;

          return (
            <div
              key={player.id}
              className={cn(
                'p-3 rounded-lg border-2 transition-all duration-200',
                isCurrentPlayer 
                  ? `${playerBorderClasses[player.color]} bg-gray-50` 
                  : 'border-gray-200'
              )}
            >
              <div className="flex items-center justify-between mb-2">
                <div className="flex items-center gap-2">
                  <div
                    className={cn(
                      'w-6 h-6 rounded-full flex items-center justify-center text-sm font-bold',
                      playerColorClasses[player.color]
                    )}
                  >
                    {player.name.charAt(0)}
                  </div>
                  <span className="font-semibold">{player.name}</span>
                  {isCurrentPlayer && (
                    <span className="text-xs bg-green-100 text-green-800 px-2 py-1 rounded-full">
                      Current Turn
                    </span>
                  )}
                </div>
                
                {isCurrentPlayer && diceValue > 0 && (
                  <div className="text-sm font-bold bg-gray-100 px-2 py-1 rounded">
                    Rolled: {diceValue}
                  </div>
                )}
              </div>

              <div className="grid grid-cols-3 gap-2 text-xs">
                <div className="text-center">
                  <div className="font-semibold text-gray-600">Home</div>
                  <div className="text-lg font-bold">{tokensAtHome}</div>
                </div>
                <div className="text-center">
                  <div className="font-semibold text-gray-600">Playing</div>
                  <div className="text-lg font-bold">{tokensOnBoard}</div>
                </div>
                <div className="text-center">
                  <div className="font-semibold text-gray-600">Finished</div>
                  <div className="text-lg font-bold">{tokensFinished}</div>
                </div>
              </div>

              {/* Token status indicators */}
              <div className="flex gap-1 mt-2 justify-center">
                {player.tokens.map((token) => (
                  <div
                    key={token.id}
                    className={cn(
                      'w-3 h-3 rounded-full border',
                      token.isHome 
                        ? 'bg-green-400 border-green-600' 
                        : token.position >= 0 
                        ? `${playerColorClasses[player.color]} opacity-80`
                        : 'bg-gray-300 border-gray-400'
                    )}
                    title={
                      token.isHome 
                        ? 'Finished' 
                        : token.position >= 0 
                        ? `Position: ${token.position}`
                        : 'At home'
                    }
                  />
                ))}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
